*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;

}



header{
  font-family: Tahoma, Verdana, Geneva, sans-serif;
  display:flex;
  justify-content: center;
  text-align: center;
  height: 280px;
  
  flex-direction: column;

}
nav{
  margin-top: 0px;
  
  
}
nav ul{
  list-style-type: none;
  display:flex;
  flex-direction: row;
  padding-top: 12px;
  justify-content: center;
  
}

nav li{
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 9px;

  
}
.hometab{
  margin-top: -40px;

}

.hometab img{
  height:160px;
  width: auto;
  transition-duration: 0.35s;
  
}

.hometab img:hover{
  height:160px;
  width: auto;
  transform: rotate(180deg);
}

.menuicon{
  width: 20px;

  margin-top: -20px;
  
}
.dropcontent{
  overflow: hidden;
  padding-top: 5px;
  position:absolute;
  z-index: 1;
  height: 0px;
  width: 85px;
  margin-left: 15px;
  transition: 0.4s ease-in;

  
}
.dropcontent h5{
  padding-bottom: 10px;
}

.dropdown{
  position: relative;
  display: inline-block;
}

.dropdown:hover .dropcontent{
  height: 110px;
}
.dropcontent h5{
  text-align: left;
  font-family: Tahoma, Verdana, Geneva, sans-serif;
}

nav h3:hover, nav h5:hover{
  color: #39ff14;
}

.title{
  text-align: left;
  margin-left: 10%;
  margin-top: -120px;
  width: 100px;
  height: 100px;
  font-family: Tahoma, Verdana, Geneva, sans-serif;
  padding-top: 40px;
  
}

.borderref{
  border-bottom: 3px solid black;
  margin-top: 55px;
}

nav a{
  text-decoration: none;
  color: black;
}

footer{
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif; 
